@import '~antd/dist/antd.css';
html, body, #root, .box {
  width: 100%;
  height: 100%;
}

.App {
  width: 1000px;
  height: 100%;
  position: absolute;
  background: #666;
  z-index: 100;
}

.box {
  position: relative;
}

header {
  width: 100%;
  height: 66px;
  background: #353030;
}

.ani {
  width: 100%;
  height: 100%;
  background: #5a5757;
  position: absolute;
}

section {
  width: 1000px;
  height: 100%;
  margin: 0 auto 0 auto;
  background: #666;
}
